




















import Vue from 'vue';
import {Component, Prop} from "vue-property-decorator";

@Component({})
export default class UgolekMobileSideMenu extends Vue {
  @Prop({required: true})
  config: any;

  showBody = false;

  toggleMenu() {
    this.showBody = !this.showBody;
  }

  showMenu() {
    this.showBody = true;
  }

  get burgerClass() {
    return {
      'burger-opened': this.showBody
    }
  }

  get sideMenuClass() {
    return {
      'ums-opened': this.showBody
    }
  }

  closeMenu() {
    this.showBody = false;
  }
}
