













import Vue from 'vue';
import {Component, Prop} from "vue-property-decorator";
import resize from 'vue-resize-directive'
//import Sticky from 'sticky-js';


interface MenuItemInterface {
  title: string;
  recid: number;
}


@Component({
  components: {

  },
  directives: {
    resize,
  }
})
export default class UgolekMenu extends Vue {
  @Prop({required: true, default: []})
  tabs!: MenuItemInterface[];

  @Prop({required: false, default: false})
  scrolling!: boolean;

  sizes: {w: number, h: number} = {w: 0, h: 0};
  hasOverflow = false;
  showAll = false;
  tabNumber = 0;

  clickTab(tab: any, idx: number) {
    this.$emit('click-tab', tab);
    this.tabNumber = idx;
  }

  tabClass(tab: any, idx: number) {
    return {
      active: idx == this.tabNumber,
      't397__tab_active': idx == this.tabNumber,
    };
  }

  clickMore() {
    this.$emit('click-more');
    this.showAll = !this.showAll;
  }

  get menuClass() {
    return {
      'show-all': this.showAll,
      'show-row': !this.showAll
    }
  }

  onResize() {
    const el = this.$refs.tabs;
    const hasOverflow = el.scrollHeight > el.offsetHeight;
    this.sizes.w = el.scrollWidth;
    this.sizes.h = el.scrollHeight;
    this.hasOverflow = hasOverflow;
    // console.log('tes', el.scrollHeight, el.scrollWidth, el.offsetHeight, el.offsetWidth);

  }
  get showMoreButton(): boolean {
      if (this.sizes.h == 0)
        return true;

      return this.hasOverflow || this.showAll;
  }

  get chevronClass() {
    return {
      'gg-chevron-up': this.showAll,
      'gg-chevron-down': !this.showAll
    };
  }
}
